<template>
  <div>
    <el-form
	  :style='{"padding":"30px","boxShadow":"0px 4px 10px 0px rgba(0,0,0,0.3020)","margin":"-30px 0 0 0","borderRadius":"0","flexWrap":"wrap","background":"#fff","display":"flex"}'
      class="add-update-preview"
      ref="ruleForm"
      :model="ruleForm"
      label-width="120px"
    >  
     <el-row>
        <el-form-item :style='{"width":"33.3%","margin":"0 0 20px 0"}'   v-if="flag=='yonghu'"  label="Account" prop="yonghuzhanghao">
          <el-input v-model="ruleForm.yonghuzhanghao" readonly              placeholder="Account" clearable></el-input>
        </el-form-item>
        <el-form-item :style='{"width":"33.3%","margin":"0 0 20px 0"}'   v-if="flag=='yonghu'"  label="Name" prop="yonghuxingming">
          <el-input v-model="ruleForm.yonghuxingming"               placeholder="Name" clearable></el-input>
        </el-form-item>
        <el-form-item :style='{"width":"33.3%","margin":"0 0 20px 0"}' v-if="flag=='yonghu'" label="Image" prop="touxiang">
          <file-upload
          tip="点击上传Image"
          action="file/upload"
          :limit="3"
          :multiple="true"
          :fileUrls="ruleForm.touxiang?ruleForm.touxiang:''"
          @change="yonghutouxiangUploadChange"
          ></file-upload>
        </el-form-item>
        <el-form-item :style='{"width":"33.3%","margin":"0 0 20px 0"}' v-if="flag=='yonghu'"  label="Gender" prop="xingbie">
          <el-select v-model="ruleForm.xingbie"  placeholder="Gender">
            <el-option
                v-for="(item,index) in yonghuxingbieOptions"
                v-bind:key="index"
                :label="item"
                :value="item">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item :style='{"width":"33.3%","margin":"0 0 20px 0"}'   v-if="flag=='yonghu'"  label="Age" prop="nianling">
          <el-input v-model="ruleForm.nianling"               placeholder="Age" clearable></el-input>
        </el-form-item>
        <el-form-item :style='{"width":"33.3%","margin":"0 0 20px 0"}'   v-if="flag=='yonghu'"  label="Telephone" prop="lianxifangshi">
          <el-input v-model="ruleForm.lianxifangshi"               placeholder="Telephone" clearable></el-input>
        </el-form-item>
        <el-form-item :style='{"width":"33.3%","margin":"0 0 20px 0"}'   v-if="flag=='banzhu'"  label="Account" prop="banzhuzhanghao">
          <el-input v-model="ruleForm.banzhuzhanghao" readonly              placeholder="Account" clearable></el-input>
        </el-form-item>
        <el-form-item :style='{"width":"33.3%","margin":"0 0 20px 0"}'   v-if="flag=='banzhu'"  label="Name" prop="banzhuxingming">
          <el-input v-model="ruleForm.banzhuxingming"               placeholder="Name" clearable></el-input>
        </el-form-item>
        <el-form-item :style='{"width":"33.3%","margin":"0 0 20px 0"}' v-if="flag=='banzhu'" label="Image" prop="touxiang">
          <file-upload
          tip="点击上传Image"
          action="file/upload"
          :limit="3"
          :multiple="true"
          :fileUrls="ruleForm.touxiang?ruleForm.touxiang:''"
          @change="banzhutouxiangUploadChange"
          ></file-upload>
        </el-form-item>
        <el-form-item :style='{"width":"33.3%","margin":"0 0 20px 0"}'   v-if="flag=='banzhu'"  label="Telephone" prop="lianxifangshi">
          <el-input v-model="ruleForm.lianxifangshi"               placeholder="Telephone" clearable></el-input>
        </el-form-item>
        <el-form-item :style='{"width":"33.3%","margin":"0 0 20px 0"}' v-if="flag=='banzhu'" label="Time" prop="chuangjianshijian">
            <el-date-picker
                value-format="yyyy-MM-dd HH:mm:ss"
                v-model="ruleForm.chuangjianshijian" 
                type="datetime"
                placeholder="Time">
            </el-date-picker>
        </el-form-item>
		<el-form-item :style='{"width":"33.3%","margin":"0 0 20px 0"}' v-if="flag=='users'" label="Username" prop="username">
			<el-input v-model="ruleForm.username" placeholder="Username"></el-input>
		</el-form-item>
		<el-form-item :style='{"width":"100%","padding":"0","margin":"0"}'>
			<el-button :style='{"border":"0","cursor":"pointer","padding":"0","margin":"0 20px 0 0","outline":"none","color":"#000","borderRadius":"0","background":"#CFD5F8","width":"128px","lineHeight":"40px","fontSize":"14px","height":"40px"}' type="primary" @click="onUpdateHandler">Modify</el-button>
		</el-form-item>
      </el-row>
    </el-form>
  </div>
</template>
<script>
// 数字，邮件，手机，url，身份证校验
import { isNumber,isIntNumer,isEmail,isMobile,isPhone,isURL,checkIdCard } from "@/utils/validate";

export default {
  data() {
    return {
      ruleForm: {},
      flag: '',
      usersFlag: false,
      yonghuxingbieOptions: [],
    };
  },
  mounted() {
    var table = this.$storage.get("sessionTable");
    this.flag = table;
    this.$http({
      url: `${this.$storage.get("sessionTable")}/session`,
      method: "get"
    }).then(({ data }) => {
      if (data && data.code === 0) {
        this.ruleForm = data.data;
      } else {
        this.$message.error(data.msg);
      }
    });
    this.yonghuxingbieOptions = "男,女".split(',')
  },
  methods: {
    yonghutouxiangUploadChange(fileUrls) {
        this.ruleForm.touxiang = fileUrls;
    },
    banzhutouxiangUploadChange(fileUrls) {
        this.ruleForm.touxiang = fileUrls;
    },
    onUpdateHandler() {
      if((!this.ruleForm.mima)&& 'yonghu'==this.flag){
        this.$message.error('Password不能为空');
        return
      }
        if(this.ruleForm.touxiang!=null) {
                this.ruleForm.touxiang = this.ruleForm.touxiang.replace(new RegExp(this.$base.url,"g"),"");
        }
      if( 'yonghu' ==this.flag && this.ruleForm.nianling&&(!isIntNumer(this.ruleForm.nianling))){
       this.$message.error(`Age应输入整数`);
        return
      }
      if( 'yonghu' ==this.flag && this.ruleForm.lianxifangshi&&(!isMobile(this.ruleForm.lianxifangshi))){
        this.$message.error(`Telephone应输入手机格式`);
        return
      }
      if( 'yonghu' ==this.flag && this.ruleForm.money&&(!isNumber(this.ruleForm.money))){
        this.$message.error(`余额应输入数字`);
        return
      }
        if(this.ruleForm.touxiang!=null) {
                this.ruleForm.touxiang = this.ruleForm.touxiang.replace(new RegExp(this.$base.url,"g"),"");
        }
      if( 'banzhu' ==this.flag && this.ruleForm.lianxifangshi&&(!isMobile(this.ruleForm.lianxifangshi))){
        this.$message.error(`Telephone应输入手机格式`);
        return
      }
      if( 'banzhu' ==this.flag && this.ruleForm.money&&(!isNumber(this.ruleForm.money))){
        this.$message.error(`余额应输入数字`);
        return
      }
      if('users'==this.flag && this.ruleForm.username.trim().length<1) {
	this.$message.error(`Username不能为空`);
        return	
      }
      this.$http({
        url: `${this.$storage.get("sessionTable")}/update`,
        method: "post",
        data: this.ruleForm
      }).then(({ data }) => {
        if (data && data.code === 0) {
          this.$message({
            message: "Modify信息成功",
            type: "success",
            duration: 1500,
            onClose: () => {
            }
          });
        } else {
          this.$message.error(data.msg);
        }
      });
    }
  }
};
</script>
