							<el-button :style='{"border":"5px solid #CFD5F8","cursor":"pointer","padding":"0 24px","margin":"0 10px 4px 0","outline":"none","color":"#000","borderRadius":"30px","background":"#fff","width":"auto","fontSize":"14px","height":"32px"}' v-if=" isAuth('remenjingdian','Modify')" type="primary" size="mini" @click="addOrUpdateHandler(scope.row.id)">Modify</el-button>





